___CSS_LOADER_EXPORT___.push([module.id, `form {
  border: 1px solid #545454;
  border-radius: 8px;
  padding: 30px;
  background-color: #f9f9f9;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 90%;
  margin: 20px auto;
}

input {
  padding: 10px 15px;
  width: 100%;
  line-height: 18px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button {
  width: 100%;
  padding: 12px;
  background-color: #007BFF;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

button:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  form {
    padding: 20px;
  }
  input, button {
    font-size: 14px;
  }
